  width: 100%;
  margin-bottom: 15px;
  border: 1px solid #F2F2F2; }
  .search-filter__field {
    width: 100%;
    padding: 10px;
    outline: none;
    border: none; }
    .search-filter__field::placeholder {
      color: #808080; }
  .search-filter__icon {
    display: flex;
